import request from '@/utils/request';

// 获取用户频道
function getChannel() {
  return request({
    method: 'GET',
    url: '/v1_0/user/channels'
  });
}

// 获取所有频道
function getAllChannel() {
  return request({
    method: 'GET',
    url: '/v1_0/channels'
  });
}

function setChannel({ channels }) {
  return request({
    method: 'PUT',
    url: '/v1_0/user/channels',
    data: {
      channels
    }
  });
}

export {
  getChannel,
  getAllChannel,
  setChannel
}