<template>
  <div class="home-container">
    <!-- 头部 -->
    <van-nav-bar title="首页" fixed />
    <!-- 内容区 -->
    <van-tabs v-model="activeName">
      <!-- 右侧图标 -->
      <div class="tabbar-right-icon" slot="nav-right" @click="show=true">
        <van-icon name="wap-nav" />
      </div>
      <!-- 频道 -->
      <van-tab :title="item.name" :name="index" v-for="(item, index) in channelList" :key="item.id">
        <!-- 下拉刷新 -->
        <van-pull-refresh v-model="pullLoading" @refresh="onRefresh">
          <!-- 频道内容区 -->
          <van-list v-model="dragLoading" :finished="finished" finished-text="没有更多了" @load="onLoad">
            <van-cell v-for="(subitem, index) in item.articles" :key="index" :title="subitem.title">
              <template slot="label">
                <!-- 文章图片 -->
                <van-grid :border="false" :column-num="3" v-if="subitem.cover.type > 0">
                  <van-grid-item
                    v-for="(imgitem, imgIndex) in subitem.cover.images"
                    :key="imgIndex"
                  >
                    <van-image :src="imgitem" lazy-load />
                  </van-grid-item>
                </van-grid>
                <!-- 文章详情 -->
                <div class="article-intro">
                  <div class="left">
                    <span>{{subitem.aut_name}}</span>
                    <span>评论 {{subitem.comm_count}}</span>
                    <span>{{subitem.pubdate | dateFormat}}</span>
                  </div>
                  <van-icon name="close" @click="openMore(subitem.art_id)" />
                </div>
              </template>
            </van-cell>
          </van-list>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
    <!-- 弹出层 -->
    <channel v-model="show" :channelArr.sync="channelList" :activeName.sync="activeName"></channel>
    <more v-model="moreShow" :artId="art_id"></more>
  </div>
</template>

<script>
import { getChannel } from "@/api/channel";
import { getArticle } from "@/api/article";
import channel from "@/views/home/channel";
import more from "@/views/home/more";
export default {
  name: "home",
  data() {
    return {
      activeName: 0,
      dragLoading: false,
      finished: false,
      pullLoading: false,
      show: false,
      moreShow: false,
      art_id: "",
      list: [],
      channelList: []
    };
  },
  methods: {
    // 上拉加载更多文章
    async onLoad() {
      // 获取当前选择的频道
      const channel = this.channelList[this.activeName];
      // 获取当前选择的频道 id
      const channel_id = channel.id;

      //
      if (channel.pre_timestamp === null) {
        this.dragLoading = false;
        this.pullLoading = false;
        this.finished = true;
        return;
      }

      // 如果 pre_timestamp = 0，这说明是第一次获取频道文章
      if (channel.pre_timestamp === 0) {
        const res = await getArticle({
          channel_id,
          timestamp: Date.now(),
          with_top: 1
        });
        // 将获取到的文章添加到当前频道的 articles 中
        channel.articles = res.results;
        // 为当前频道添加获取下一页文章的时间戳
        channel.pre_timestamp = res.pre_timestamp;
      } else {
        const res = await getArticle({
          channel_id,
          timestamp: channel.pre_timestamp,
          with_top: 1
        });
        // 为当前频道添加获取下一页文章的时间戳
        channel.pre_timestamp = res.pre_timestamp;
        // 将新获取的文章添加到 articles 数组中
        channel.articles = [...channel.articles, ...res.results];
      }
      this.dragLoading = false;
      this.pullLoading = false;
      this.finished = false;
    },
    // 下拉刷新文章
    async onRefresh() {
      // 获取当前选择的频道
      const channel = this.channelList[this.activeName];
      // 获取当前选择的频道 id
      const channel_id = channel.id;
      // 清除当前频道的所有文章和重置文章获取的时间戳
      channel.articles = [];
      channel.pre_timestamp = 0;
      // 获取新的文章
      const res = await getArticle({
        channel_id,
        timestamp: Date.now(),
        with_top: 1
      });
      channel.articles = res.results;
      this.dragLoading = false;
      this.pullLoading = false;
      this.finished = false;
    },
    // 获取频道
    async getChannel() {
      // 获取 localStorage 中的 user
      let { user } = this.$store.state;
      // 判断用户是否登陆，如果登陆，则将其中的 channels 存放到 channelList 中
      if (user) {
        const res = await getChannel();
        this.channelList = res.channels;
      } else {
        // 获取频道缓存
        const channelCache = JSON.parse(
          window.localStorage.getItem("channels")
        );
        // 如果频道缓存存在，则将其中的 channels 存放到 channelList 中
        if (channelCache) {
          this.channelList = channelCache;
        } else {
          // 如果缓存不存在，则使用系统默认频道
          const res = await getChannel();
          this.channelList = res.channels;
        }
      }

      // 获取频道后，为每一个频道对象添加数据
      this.setChannel();
    },
    // 为频道添加数据
    async setChannel() {
      this.channelList.forEach(item => {
        // 添加文章列表
        this.$set(item, "articles", []);
        // 添加前一页的时间戳
        this.$set(item, "pre_timestamp", 0);
      });
    },
    // 打开文章的更多的对话框
    openMore(art_id) {
      this.art_id = art_id;
      this.moreShow = true;
    }
  },
  async created() {
    this.getChannel();
  },
  components: {
    channel,
    more
  }
};
</script>

<style lang="less">
.home-container {
  margin-bottom: 50px;
  margin-top: 46px;

  .van-tabs__wrap {
    position: fixed;
    top: 47px;
    left: 0px;
    z-index: 999;
    width: 100%;

    .van-tabs__nav {
      margin-right: 40px;
    }
  }

  .van-tabs__content {
    margin-top: 91px;

    .van-cell {
      // height: 80px;
      .article-intro {
        display: flex;
        justify-content: space-between;

        .left {
          span {
            margin-right: 10px;
          }
        }
        i {
          font-size: 16px;
        }
      }
    }
  }

  .tabbar-right-icon {
    position: fixed;
    top: 47px;
    right: 0px;
    width: 40px;
    height: 44px;
    text-align: center;
    background-color: #fff;

    i {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
</style>