<template>
  <div class="more-container">
    <van-dialog :value="value" :showConfirmButton="false">
      <van-cell-group v-if="itemShow">
        <van-cell @click="$emit('input', false)" icon="arrow-left" title="返回" />
        <van-cell title="隐藏此信息" @click="addBlacklist" clickable />
        <van-cell title="举报" is-link @click="itemShow=false" />
        <van-cell title="拉黑" clickable @click="addUserBlacklist" />
      </van-cell-group>
      <van-cell-group v-if="!itemShow">
        <van-cell @click="itemShow=true" icon="arrow-left" title="返回" />
        <van-cell
          :title="item.name"
          v-for="(item, index) in itemList"
          :key="index"
          clickable
          @click="report(item.value)"
        />
      </van-cell-group>
    </van-dialog>
  </div>
</template>

<script>
import { hideArticle, reportArticle } from "@/api/article";
export default {
  data() {
    return {
      show: false,
      itemShow: true,
      itemList: [
        { value: 0, name: "其他问题" },
        { value: 1, name: "标题夸张" },
        { value: 2, name: "低俗色情" },
        { value: 3, name: "错别字多" },
        { value: 4, name: "旧闻重复" },
        { value: 5, name: "广告软文" },
        { value: 6, name: "内容不实" },
        { value: 7, name: "涉嫌违法犯罪" },
        { value: 8, name: "侵权" }
      ]
    };
  },
  methods: {
    // 屏蔽文章
    async addBlacklist() {
      try {
        await hideArticle(this.artId);
        this.$emit("input", false);
        this.$toast.success("成功屏蔽文章");
      } catch (error) {
        this.$emit("input", false);
        this.$toast.fail("屏蔽文章失败");
      }
    },
    // 举报
    async report(type) {
      try {
        await reportArticle({
          target: this.artId,
          type
        })
        this.$toast.success('举报成功');
      } catch (error) {
        if (error.message.indexOf('409')) {
          this.$toast.fail('已举报，请勿重复举报');
        } else {
          this.$toast.fail('系统异常');
        }
      } finally {
        this.itemShow = true;
        this.$emit("input", false);
      }
    },
    // 屏蔽用户
    addUserBlacklist() {
      this.$emit("input", false);
    }
  },
  props: ["value", "artId"]
};
</script>

