<template>
  <div class="channel-container">
    <van-popup
      :value="value"
      @input="$emit('input', $event)"
      position="bottom"
      round
      :style="{ height: '85%' }"
      @close="closePopup"
    >
      <!-- 我的频道 -->
      <div class="channel-box">
        <!-- 头部标题 -->
        <div class="top">
          <div class="title">
            我的频道
            <span class="sub-title">点击进入频道</span>
          </div>
          <van-button
            v-if="isEdit"
            round
            type="warning"
            size="mini"
            plain
            @click="editingChannel"
          >编辑</van-button>
          <van-button v-else round type="warning" size="mini" plain @click="editChannel">完成</van-button>
        </div>
        <!-- 九宫格 -->
        <van-grid clickable gutter="7px">
          <van-grid-item
            class="grid-text"
            v-for="(item, index) in channelArr"
            :key="item.id"
            @click="changeActive(index)"
          >
            <div :class="{current: index === activeName}">
              {{item.name}}
              <van-icon name="clear" v-if="!isEdit && index !== 0" @click="removeChannel(index)" />
            </div>
          </van-grid-item>
        </van-grid>
      </div>
      <!-- 频道推荐 -->
      <div class="channel-box">
        <!-- 头部标题 -->
        <div class="top">
          <div class="title">
            频道推荐
            <span class="sub-title">点击添加频道</span>
          </div>
        </div>
        <!-- 九宫格 -->
        <van-grid clickable gutter="10px">
          <van-grid-item
            class="grid-text"
            v-for="item in otherChannel"
            :key="item.id"
            @click="addChannel(item)"
          >{{item.name}}</van-grid-item>
        </van-grid>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { getAllChannel, setChannel } from "@/api/channel";
export default {
  data() {
    return {
      isEdit: true,
      channelAll: [],
      fakerChannel: []
    };
  },
  methods: {
    // 获取点击频道的索引
    changeActive(index) {
      this.$emit("update:activeName", index);
    },
    // 编辑频道按钮
    editingChannel() {
      this.fakerChannel = [...this.channelArr];
      this.isEdit = false;
    },
    // 关闭弹出框
    closePopup() {
      if (!this.isEdit) {
        this.$emit("update:channelArr", this.fakerChannel);
        this.isEdit = true;
      }
    },
    // 保存频道编辑
    async editChannel() {
      let user = this.$store.state.user;
      if (!user || !user.token) {
        if (!this.isEdit) {
          window.localStorage.setItem(
            "channels",
            JSON.stringify(this.channelArr)
          );
        }
      } else {
        if (!this.isEdit) {
          let channels = this.channelArr.slice(1).map((item, index) => {
            return {
              id: item.id,
              seq: index + 2
            };
          });
          await setChannel({ channels });
        }
      }
      this.isEdit = true;
    },
    // 获取所有频道
    async getAllChannelList() {
      let res = await getAllChannel();
      res.channels.forEach(item => {
        // 添加文章列表
        this.$set(item, "articles", []);
        // 添加前一页的时间戳
        this.$set(item, "pre_timestamp", 0);
      });
      this.channelAll = res.channels;
    },
    // 将频道推荐的频道添加到我的频道
    async addChannel(item) {
      this.channelArr.push(item);
      let user = this.$store.state.user;
      if (!user || !user.token) {
        window.localStorage.setItem(
          "channels",
          JSON.stringify(this.channelArr)
        );
      } else {
        let channels = this.channelArr.slice(1).map((item, index) => {
          return {
            id: item.id,
            seq: index + 2
          };
        });
        await setChannel({ channels });
      }
    },
    // 移除我的频道里的频道
    async removeChannel(index) {
      // let user = this.$store.state.user;
      this.channelArr.splice(index, 1);
      // if (!user || !user.token) {
      //   this.editChannel();
      //   // window.localStorage.setItem(
      //   //   "channels",
      //   //   JSON.stringify(this.channelArr)
      //   // );
      // } else {
      //   let channels = this.channelArr.slice(1).map((item, index) => {
      //     return {
      //       id: item.id,
      //       seq: index + 2
      //     };
      //   });
      //   await setChannel({ channels });
      // }
    }
  },
  props: ["value", "channelArr", "activeName"],
  computed: {
    // 获取我的频道里不包含的频道
    otherChannel() {
      const channelId = this.channelArr.map(item => item.id);
      let otherchannel = this.channelAll.filter(
        item => !channelId.includes(item.id)
      );
      return otherchannel;
    }
  },
  created() {
    this.getAllChannelList();
  }
};
</script>

<style lang="less">
.channel-container {
  .channel-box {
    margin: 22px 12px;
    .top {
      display: flex;
      justify-content: space-between;
      .title {
        font-size: 16px;
        .sub-title {
          font-size: 12px;
          color: #aaa;
        }
      }
      .van-button__text {
        font-size: 12px;
      }
    }
    .grid-text {
      position: relative;
      .current {
        color: red;
      }
      i {
        position: absolute;
        top: -5px;
        right: -5px;
        font-size: 20px;
        color: #333;
        z-index: 10;
      }
    }
    .van-grid-item__content {
      padding: 8px 0px;
      margin-top: 5px;
      font-size: 12px;
    }
    [class*="van-hairline"]::after {
      border: 1px solid #333;
    }
  }
}
</style>